import React, { Component } from 'react';

import './footer.css'

export default class Footer extends Component{
    render(){
        return(
            <div>
                <nav className = 'main'>
                    <li><i class="fas fa-star"></i><p>Favourites</p></li>
                    <li><i class="fas fa-clock"></i><p>Recents</p></li>
                    <li id = 'blue'><i class="fas fa-address-book"></i><p>Contacts</p></li>
                    <li><i class="far fa-keyboard"></i><p>Keypad</p></li>
                    <li><i class="fas fa-phone"></i><p>Voicemail</p></li>
                </nav>
            </div>
        )
    }
}