import React, { Component } from 'react';

import Contact from './contact'
import Footer from './footer'

import './contacts.css'
const icon = 'https://upload.wikimedia.org/wikipedia/commons/f/f4/User_Avatar_2.png';
const contacts = [{
    firstName: "Владислав",
    lastName: "Пасемко",
    phone: "+380505184809",
    gender: "male"
}, {
    firstName: "Робин",
    lastName: "Щербатская",
    phone: "+380931460123",
    gender: "female"
}, {
    firstName: "Аномный",
    lastName: "Анонимус",
    phone: "+380666666666"
}, {
    firstName: "Лилия",
    lastName: "Олдровна",
    phone: "+380504691254",
    gender: "female"
}, {
    firstName: "Маршэн",
    lastName: "Эриксонян",
    phone: "+380739432123",
    gender: "male"
}, {
    firstName: "Теодор",
    lastName: "Мотсбэс",
    phone: "+380956319521",
    gender: "male"
}];


contacts.forEach((contact) => {
    contact.firstName = contact.firstName + ' '
    contact.lastName = contact.lastName + ' '
})


export default class Contacts extends Component{
    state = {
        contacts,
        search: '',
        male: true,
        female: true,
        none: true
    }

    handleSearchChange= (e) => {
        this.setState({
            search: e.target.value,
        })
    }

    male_click = () =>{
        this.setState({
            male: !this.state.male
        })
    }

    female_click = () =>{
        this.setState({
            female: !this.state.female
        })
    }
    
    none_click = () =>{
        this.setState({
            none: !this.state.none
        })
    }


    render(){
        let elements = 'elements';
        const info = this.state.contacts;

        let genders = [];
        const gendered = info.map(elem=>{
            if((this.state.male)&(elem.gender === 'male')){
                genders.push(elem);
            }
            else if((this.state.female)&(elem.gender === 'female')){
                genders.push(elem);
            }
            else if((this.state.none)&(!elem.gender)){
                genders.push(elem);
            }
        })

        const rendered_contacts = genders.map(curr=>{
            if(((curr.firstName+''+curr.lastName).toLowerCase().includes(this.state.search))||
            (curr.phone.includes(this.state.search))){
            return(
                <Contact 
                    {...curr} 
                />
            )
        }
    })

        return(
            <div className = "container">
                <div className = "header">
                    <span>Groups</span>
                    <i className="fas fa-plus "></i>  
                </div>
                <h1 className = 'contacts'>Contacts</h1>    
                <div className = "search">
                    <button className = 'search_icon'>
                        <i className="fas fa-search "></i>
                    </button>
                    <input onChange = {this.handleSearchChange} value = {this.state.search} className = 'input' placeholder = 'Search' type="text"/>
                </div>
                <div className = 'filter'>
                    <div className = 'checkbox'>
                        <i class="fas fa-male blue"></i>
                        <input onClick = {this.male_click} type="checkbox" defaultChecked/>
                    </div>
                    <div className = 'checkbox'>
                         <i class="fas fa-female pink"></i>
                         <input onClick = {this.female_click} type="checkbox" defaultChecked/>
                    </div>
                    <div className = 'checkbox'>
                        <i class="fas fa-genderless green"> </i>
                        <input onClick = {this.none_click} type="checkbox" defaultChecked/>
                    </div>
                </div>
                <div className = "profile">
                    <img src = {icon} />
                    <div>
                        <h1>Vlad Pasemko</h1>
                        <p>My Card</p>
                    </div>
                </div>
                <div className = {elements}>{rendered_contacts}</div>
                <div className = 'footer'><Footer/></div>     
            </div>
        )
    } 
}