import React, { Component } from 'react';
import './contact.css'
export default class Contact extends Component{
    state = {
        hide: true
    }

    showinfo = () =>{
        this.setState({
            hide: !this.state.hide
        })
    }   

    render(){
        const{firstName,lastName,phone,gender="not defined"} = this.props;
        
        let classNames = 'hide';
        let hide = this.state.hide;
        if(!hide){
            classNames = 'show'
        };

        const getgender = () =>{
            if (gender === 'male'){
                return (<i class="fas fa-male"></i>)  
            }
            else if (gender === 'female'){
                return (<i class="fas fa-female"></i>)
            }
            else{
                return gender;
            }
        }
        
        return(
            <div className = 'name' >
             <p onClick = {this.showinfo}> <span>{firstName}</span> <span className = 'surname'>{lastName}</span> </p>
             <div className = {classNames}>
                <p><span className = 'atr'>Phone: </span>{phone}</p> 
                <p><span className = 'atr'>Gender: </span> {getgender()}</p> 
             </div>
            </div>
        )
    }
    


}